#menu {
  position: fixed;
  height: 50vh;
  height: calc(var(--vh, 1vh) * 50);
  padding: 10vh 40px 20px 40px;
  padding-top: calc(var(--vh, 1vh) * 10);
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  a {
    margin: 10px 0;
    font-size: 18px;
  }

  .media__icons {
    display: flex;
    margin: auto 0 0 auto;
    svg {
      transform: scale(1.4);
      fill: $text-color;
      margin: 0 20px;
      &:nth-of-type(1) {
        margin-left: 0;
      }
    }
  }
}
